
/*
================================================================================
|                                     FOOTER                                   |
================================================================================
*/
footer{
	order: 3;	
    -webkit-order: 3;	
}
.footer{
	background: #123454;
	padding: 80px 0 30px;
	position: relative;
	&__bg{
		position: absolute;
		left: -45px;
		bottom: -155px;
	}
	.wrapper{
		&.flex{
			align-items: flex-start;
		}
	}
	&__item{
		width: 33%;
		position: relative;
	}
	&__programm{
		padding-right: 40px;
		&:after{
			content: "";
			width: 1px;
			height: 210px;
			background: #fff;
			position: absolute;
			right: 0;
			top: -8px;
		}
		.footer{
			&__title{
				text-align: right;
			}
		}
	}
	&__subs{
		padding-left: 30px;
		&:after{
			content: "";
			width: 1px;
			height: 210px;
			background: #fff;
			position: absolute;
			right: 0;
			top: -8px;
		}
	}
	&__connect{
		padding-left: 40px;
	}
	&__title{
		font: 300 24px $font1;
		color: #fff;
		margin-bottom: 20px;
	}
	&__btn{
		display: block;
		margin-left: auto;
		margin-right: 0;
		width: 225px;
		height: 44px;
		font: 300 18px $font1;
		color: #fff;
		border: 1px solid #fff;
		position: relative;
		background: none;
		transform: perspective(1px) translateZ(0);
		transition: all 0.5s;
		&:before{
			content: "";
		    position: absolute;
		    z-index: -1;
		    top: 0;
		    left: 0;
		    right: 0;
		    bottom: 0;
		    background: #fff;
		    transform: scaleX(0);
		    transform-origin: 0 50%;
		    transition: all 0.5s ease-out;
		}
		&:hover{
			color: #123454;
			&:before{
				transform: scaleX(1);
				transition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66);
			}
		}
	}
	&__form{
		width: 100%;
		&.flex{
			justify-content: flex-start;
		}
		input{
			width: 240px;
			height: 44px;
			font: 300 18px $font1;
			color: #fff;
			background: none;
			border: 0;
			border-bottom: 1px solid #fff;
			transition: all 0.5s ease;
			&::placeholder{
				font: 300 18px $font1;
				color: #fff;
				opacity: 0.6;
			}
			&:focus{
				padding-left: 10px;
			}
		}
		button{
			width: 46px;
			height: 46px;
			border-radius: 100%;
			background: #ffcc32;
			font: 300 18px $font1;
			color: #123454;
			transition: all 0.5s ease;
			border: 0;
			margin-left: 22px;
			&:hover{
				background: #fff;
			}
		}
	}
	&__soc{
		margin-bottom: 25px;
		&.flex{
			justify-content: flex-start;
		}
		a{
			width: 46px;
			height: 46px;
			background: #fff;
			border-radius: 100%;
			margin-right: 10px;
			transition: all 0.5s ease;
			&.flex{
				justify-content: center;
				align-items: center;
			}
			&:hover{
				transform: rotate(360deg);
			}
		}
	}
	&__text{
		font: 300 16.5px $font1;
		color: #fff;
	}
	&__info{
		width: 100%;
		margin-top: 90px;
		text-align: center;
		p,a{
			font: 300 17px/20px $font1;
			color: #fff;
		}
		a{
			text-decoration: underline;
		}
	}
}



@media screen and (min-width: 992px) and (max-width: 1200px) {
	.footer{
		&__form{
			input{
				width: 180px;
			}
		}
	}
}

@media screen and (min-width: 768px) and (max-width: 991px) {
	.footer{
		&__item{
			width: 50%;
			padding: 0;
		}
		&__programm{
			.footer{
				&__title{
					text-align: center;
				}
			}
			&:after{
				height: 115px;
			}
		}
		&__btn{
			margin-right: auto;
		}
		&__subs{
			text-align: center;
			&:after{
				content: none;
			}
		}
		&__form{
			&.flex{
				justify-content: center;
			}
		}
		&__connect{
			width: 100%;
    		margin-top: 35px;
    		text-align: center;
		}
		&__soc{
			&.flex{
				justify-content: center;
			}
		}
	}
}

@media screen and (max-width: 767px) {
	.footer{
		padding: 50px 0 30px;
		&__bg{
			width: 200px;
    		left: -40px;
    		bottom: -77px;
		}
		&__item{
			width: 100%;
			padding: 0;
			margin-bottom: 40px;
		}
		&__programm{
			.footer{
				&__title{
					text-align: center;
				}
			}
			&:after{
				content: none;
			}
		}
		&__btn{
			margin-right: auto;
		}
		&__subs{
			text-align: center;
			&:after{
				content: none;
			}
		}
		&__form{
			&.flex{
				justify-content: center;
			}
			input{
				width: 205px;
			}
		}
		&__connect{
			width: 100%;
    		/*margin-top: 35px;*/
    		text-align: center;
		}
		&__soc{
			&.flex{
				justify-content: center;
			}
		}
		&__info{
			margin-top: 30px;
			p, a{
				font-size: 16px;
			}
		}
	}
}