/*
================================================================================
|                                     CONTENT                                 |
================================================================================
*/
.content{	
	order: 2;
    -webkit-order: 2;
}
.flex{
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
}
.title{
	width: 100%;
	text-align: center;
	position: relative;
	overflow: hidden;
	padding-bottom: 27px;
	margin-bottom: 65px;
	h2{
		color: #123454;
		font: 300 38px $font2;
	}
	img{
		position: absolute;
		bottom: 0;
		left: 50%;
		margin-left: -607px;
	}
}
.btn{
	display: block;
	width: 315px;
	height: 87px;
	font: 300 24px $font1;
	color: #f0f0eb;
	border: 0;
	background: #123454;
	transform: perspective(1px) translateZ(0);
	position: relative;
	transition: all 0.5s;
	margin: 0 auto;
	&:before{
		content: "";
	    position: absolute;
	    z-index: -1;
	    top: 0;
	    left: 0;
	    right: 0;
	    bottom: 0;
	    background: #ffcc32;
	    transform: scaleX(0);
	    transform-origin: 0 50%;
	    transition: all 0.5s ease-out;
	}
	&:hover{
		&:before{
			transform: scaleX(1);
			transition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66);
		}
	}
}
.banner{
	background: #fafaf6;
	position: relative;
	overflow: hidden;
	padding: 205px 0;
	&__bg{
		position: absolute;
		right: -80px;
		bottom: -155px;
	}
	&__cont{
		&.flex{
			align-items: flex-start;
		}
	}
	&__left{
		width: 50%;
		padding-right: 75px;
		position: relative;
		&:before{
			content: "";
			width: 5px;
			height: 420px;
			background: #fed559;
			position: absolute;
			right: -2.5px;
			top: 50%;
			margin-top: -205px;
		}
	}
	&__title{
		font: 700 35px $font1;
		color: #123454;
		text-align: right;
	}
	&__right{
		width: 50%;
		padding-left: 70px;
		padding-top: 5px;
	}
	&__text{
		font: 400 24px/32px $font2;
		color: #123454;
		margin-bottom: 35px;
		&:last-child{
			margin-bottom: 0;
		}
	}
}
.members{
	background: #f0f0eb;
	padding: 75px 0 70px;
	&__slider{
		margin-bottom: 80px;
		.slick-slide{
			margin: 0 20px;
		}
		.slick-list{
			margin: 0 -20px;
		}
		.slick-prev, .slick-next{
			width: 14px;
			height: 27px;
			background: url(../img/prev.png);
			position: absolute;
			top: 50%;
			margin-top: -13.5px;
			text-indent: 9999px;
			border: 0;
			z-index: 3;
			transition: all 0.3s ease;
			&:hover{
				filter: brightness(30%);
			}
		}
		.slick-prev{
			left: -40px;
		}
		.slick-next{
			right: -40px;
			transform: rotate(180deg);
		}
	}
	&__item{
		background: #fafaf6;
		padding: 25px 0 190px;
		position: relative;
		overflow: hidden;
		cursor: pointer;
		&:hover{
			.members{
				&__info{
					top: 0;
					height: 100%;
					background: #ffcc32;
					p{
						opacity: 1;
					}
				}
			}
		}
	}
	&__img{
		width: 236px;
		height: 236px;
		margin: 0 auto 0;
		img{
			max-width: 100%;
		}
	}
	&__info{
		padding: 20px 25px;
		background: #fafaf6;
		position: absolute;
		width: 100%;
		top: 263px;
		transition: all 0.3s ease;
		h3{
			font: 700 24px/28px $font1;
			color: #123454;
			margin-bottom: 10px;
		}
		h5{
			font: 300 22px $font1;
			color: #123454;
		}
		p{
			font: 300 18px/22px $font1;
			color: #123454;
			margin-bottom: 15px;
			opacity: 0;
			transition: all 0.3s ease;
		}
	}
	&__border{
		width: 100%;
		height: 1px;
		background: #fafaf6;
		margin: 18px 0 15px;
	}
	&__more{
		display: block;
		width: 100%;
		text-align: right;
		img{
			margin-right: 0;
			margin-left: auto;
		}
	}
}





@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    body{min-height: 100hv; width: 100%; display: flex; flex-direction: column;}
}
@media screen and (min-width: 992px) and (max-width: 1200px) {
	.wrapper{
		max-width: 940px;
	}
	.banner{
		padding: 150px 0;
		&__bg{
			width: 350px;
    		right: -60px;
    		bottom: -145px;
		}
		&__title{
			font-size: 29px;
		}
		&__left{
			padding-right: 50px;
			&:before{
				margin-top: -180px;
				width: 4px;
    			height: 360px;
    			margin-left: -2px;
			}
		}
		&__right{
			padding-left: 50px;
		}
		&__text{
			font-size: 21px;
			line-height: 28px;
		}
	}
	.title{
		margin-bottom: 50px;
	}
	.members{
		padding: 55px 0 50px;
		&__slider{
			width: 900px;
			margin-left: auto;
			margin-right: auto;
			margin-bottom: 50px;
		}
	}
}

@media screen and (min-width: 768px) and (max-width: 991px) {
	.wrapper{
		max-width: 740px;
	}
	.title{
		margin-bottom: 30px;
	}
	.banner{
		&__left{
			margin-bottom: 30px;
			width: 100%;
    		padding-right: 0;
    		&:before{
    			width: 420px;
    			height: 4px;
    			right: auto;
			    left: 50%;
			    margin-left: -210px;
			    top: auto;
			    margin-top: 0;
			    bottom: -17px;
    		}
		}
		&__title{
			text-align: center;
		}
		&__right{
			width: 100%;
    		padding-left: 0;
		}
		&__text{
			text-align: center;
		}
	}
	.members{
		padding: 55px 0 50px;
		&__slider{
			width: 700px;
			margin-left: auto;
			margin-right: auto;
			margin-bottom: 50px;
			.slick-slide{
				margin: 0 10px;
			}
			.slick-list{
				margin: 0 -10px;
			}
			.slick-prev{
				left: -30px;
			}
			.slick-next{
				right: -30px;
			}
		}
	}
}

@media screen and (max-width: 767px) {
	.wrapper{
		max-width: 90%;
	}
	.title{
		margin-bottom: 30px;
	}
	.btn{
		width: 290px;
	}
	.banner{
		padding: 65px 0;
		&__bg{
			width: 200px;
			right: -30px;
			bottom: -80px;
		}
		&__left{
			margin-bottom: 30px;
			width: 100%;
    		padding-right: 0;
    		&:before{
    			width: 260px;
    			height: 2px;
    			right: auto;
			    left: 50%;
			    margin-left: -130px;
			    top: auto;
			    margin-top: 0;
			    bottom: -17px;
    		}
		}
		&__title{
			text-align: center;
			font-size: 20px;
		}
		&__right{
			width: 100%;
    		padding-left: 0;
		}
		&__text{
			text-align: center;
			font-size: 16px;
			line-height: 22px;
		}
	}
	.members{
		padding: 55px 0 50px;
		&__slider{
			width: 100%;
			margin-left: auto;
			margin-right: auto;
			margin-bottom: 50px;
			.slick-slide{
				margin: 0 10px;
			}
			.slick-list{
				margin: 0 -10px;
			}
			.slick-prev{
				left: 40%;
			    top: auto;
			    margin-top: 0;
			    bottom: -35px;
			}
			.slick-next{
				right: 40%;
			    top: auto;
			    margin-top: 0;
			    bottom: -35px;
			}
		}
		&__info{
			h3{
				font-size: 22px;
				line-height: 26px;
			}
		}
	}
}



